<template>
  <div class="hello">
    <p>一个下拉框</p>
    <v-select :btnValue='cx' :list='list1'></v-select>
    <p>另一个下拉框</p>
    <v-select :btnValue='ss' :list='list2'></v-select>
  </div>
</template>
<script>
import select from './select.vue'
export default {
  name: 'hello',
  data() {
    return {
      cx: '查询',
      ss: '搜索',
      list1:['武汉','广州','上海'],
      list2:['4-17','4-18','4-19']
    }
  },
  components: {
    'v-select': select
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  width: 200px;
}
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



