<template>
  <div class="body">
    <div class="select">
      <input type='text' id="parent" @click='selectShow = !selectShow' :value='val'></input>
      <input class="tijiao" type="button" :value="btnValue">
    </div>
    <ul v-show='selectShow' >
      <li v-for='item in list' @click='selectValue(item)'>{{item}}</li>
    </ul>
  </div>
</template>
<script>
export default {
  data(){
    return {
      selectShow:false,
      val:''
    }
  },
   props:['btnValue','list'],
   methods:{
    selectValue(item){
      this.val = item
      this.selectShow = false
    }

   }

}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.body {
  width: 200px;
}

.select {
  position: relative;
}

section {
  border-radius: 5px;
  background: #f7f7f7;
}

.tijiao {
  top: 2px;
  left: 105px;
  height: 28px;
}

ul {
  margin-top: 5px;
}

li {
  width: 80px;
  list-style: none;
}

li:hover {
  border-radius: 8px;
  background: green;
}

#parent {
  width: 100px;
  height: 30px;
  border: solid 1px #ccc;
}
</style>



